td.tile {
  background-position: center center;
  background-repeat: no-repeat;
  height: 200px;
  max-height: 200px;
  max-width: 200px;
  overflow: hidden;
  width: 200px;
}

/* Set the image to fill its parent and make transparent */
td.tile img {
  display:block; 
  /* IE 5-7 */
  filter: alpha(opacity=0);
  height:auto;
  min-height: 100%;
  min-width: 100%;
  width:100%; 
  /* IE 8 */
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  /* modern browsers */
  opacity: 0;
}


